import React from "react"
import { Link, useStaticQuery, graphql } from "gatsby"
import { rhythm, scale } from "../utils/typography"
import { render } from "react-dom"

export const Tslider= () => {
  // useStaticQuerygraphqldata
  const data = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title          
          }
        }
        allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
          edges {
            node {
              excerpt
              fields {
                slug
              }
              frontmatter {
                date(formatString: "MMMM DD, YYYY")
                title
                description
              }
            }
          }
        }
      }
    ` 
  )

  const posts = data.allMarkdownRemark.edges
  //const postslen = Object.keys(posts).length

  //for ( var i = 0; i < postslen; i++ ) {
  //  console.log("i " + i);
  //  const title = posts[i].node.frontmatter.title || posts[i].node.fields.slug
  //  console.log(" " + title);
  //  return (
  //      <h2>{title}</h2>
  //  )
  //}

  var list = [];
  {posts.map(({ node }) => {
    const title = node.frontmatter.title || node.fields.slug
    //const title = node.edges.frontmatter.title || node.edges.fields.slug
    list.push(title);
  })}
  console.log("", list);

  const title1 = posts[0].node.frontmatter.title || posts[0].node.fields.slug
  const title2 = posts[1].node.frontmatter.title || posts[1].node.fields.slug
  const title3 = posts[2].node.frontmatter.title || posts[2].node.fields.slug
  const title4 = posts[3].node.frontmatter.title || posts[3].node.fields.slug
  const title5 = posts[4].node.frontmatter.title || posts[4].node.fields.slug
  return (
    <div class="my-slider">
        <div>
            <h3
              style={{
                marginBottom: rhythm(1 / 4),
              }}
            >
              <Link style={{ boxShadow: `none` }}>
                  {title1}
                  {list[0]}
              </Link>
            </h3>
        </div>
        <div>
            <h3
              style={{
                marginBottom: rhythm(1 / 4),
              }}
            >
              <Link style={{ boxShadow: `none` }}>
                  {title2}
              </Link>
            </h3>
        </div>
        <div>
            <h3
              style={{
                marginBottom: rhythm(1 / 4),
              }}
            >
              <Link style={{ boxShadow: `none` }}>
                  {title3}
              </Link>
            </h3>
        </div>
        <div>
            <h3
              style={{
                marginBottom: rhythm(1 / 4),
              }}
            >
              <Link style={{ boxShadow: `none` }}>
                  {title4}
              </Link>
            </h3>
        </div>
        <div>
            <h3
              style={{
                marginBottom: rhythm(1 / 4),
              }}
            >
              <Link style={{ boxShadow: `none` }}>
                  {title5}
              </Link>
            </h3>
        </div>
    </div>
  )
}
